export default {
	data() {
		return {
			sizeCalcState:false,
			currentId:1,
			newsList: [],
			tabIndex: 0,
			title:'全部',
			dataGoods:[],
			data:[],
			hasOrder: true,
			tabBars: [{
				name: '出售中',
				id: 'insale'
			}, {
				name: '售罄的',
				id: 'saleout'
			}, {
				name: '仓库中',
				id: 'storehouse'
			}],
			scrollInto: "",
			showTips: false,
			navigateFlag: false,
			pulling: false,
			goodsType: [{
					name: '全部',
					active: true
				},
				{
					name: '手机',
					active: false
				},
				{
					name: '电脑',
					active: false
				},
				{
					name: '家居',
					active: false
				},
				{
					name: '服饰',
					active: false
				},
				{
					name: '数码',
					active: false
				}
			],
			goodslist:[
				{
					goodsicon:'../../static/img/phone11_promax.jpg',
					goodsname:'phone11 pro max',
					goodsprice:'8000',
					goodsstock:'10000',
					stockunit:'台',
					goodssales:'2129'
				},{
					goodsicon:'../../static/img/mate30_rs.jpg',
					goodsname:'华为mate30 rs 保时捷版',
					goodsprice:'18000',
					goodsstock:'10000',
					stockunit:'台',
					goodssales:'2129'
				}
			]
		}
	},
	onLoad: function(option) {
		setTimeout(() => {
			this.tabBars.forEach((tabBar) => {
				this.newsList.push({
					data: [],
					isLoading: false,
					refreshText: "",
					loadingText: '加载更多...'
				});
			});
			this.getList(0);
		}, 350)
	},
	methods: {
		addgoods(){
			uni.navigateTo({
				url:'./addgoods'
			})
		},
		ontabtap(e) {
			let index = e.target.dataset.current || e.currentTarget.dataset.current;
			this.switchTab(index);
		},
		ontabchange(e) {
			let index = e.target.current || e.detail.current;
			this.switchTab(index);
		},
		switchTab(index) {
			if (this.tabIndex === index) {
				return;
			}
			this.tabIndex = index;
			this.scrollInto = this.tabBars[index].id;

		},
		clearTabData(e) {
			this.newsList[e].data.length = 0;
			this.newsList[e].loadingText = "加载更多...";
		},
		refreshData() {},
		onrefresh(e) {
			var tab = this.newsList[this.tabIndex];
			if (!tab.refreshFlag) {
				return;
			}
			tab.refreshing = true;
			tab.refreshText = "正在刷新...";

			setTimeout(() => {
				this.refreshData();
				this.pulling = true;
				tab.refreshing = false;
				tab.refreshFlag = false;
				tab.refreshText = "已刷新";
				setTimeout(() => { // TODO fix ios和Android 动画时间相反问题
					this.pulling = false;
				}, 500);
			}, 2000);
		},
		onpullingdown(e) {
			var tab = this.newsList[this.tabIndex];
			if (tab.refreshing || this.pulling) {
				return;
			}
			if (Math.abs(e.pullingDistance) > Math.abs(e.viewHeight)) {
				tab.refreshFlag = true;
				tab.refreshText = "释放立即刷新";
			} else {
				tab.refreshFlag = false;
				tab.refreshText = "下拉可以刷新";
			}
		},
		//左边导航栏点击
		goodsTypeTap(index){
			for(let i in this.goodsType){
				this.goodsType[i].active=false;
			}
			this.dataGoods=[];
			this.goodsType[index].active=true;
			this.title=this.goodsType[index].name;
			if(this.title=='全部')
				this.dataGoods=this.data
			else{
				for(let i in this.data){
					if(this.data[i].goodsType==this.title){
						this.dataGoods.push(this.data[i])
					}
				}
			}
		}
	}
}
